import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import '../styles/Edit.css';

const Edit = () => {
  const { memberId } = useParams();
  const [nickname, setNickname] = useState('');
  const [password, setPassword] = useState('');
  const [actorList, setActorList] = useState([]);
  const [newActor, setNewActor] = useState('');
  const [genreList, setGenreList] = useState([]);
  const [newGenre, setNewGenre] = useState('');
  const [loading, setLoading] = useState(true);

  const navigate = useNavigate();

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const token = localStorage.getItem('token');
        const response = await fetch(`/member/user/${memberId}`, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (response.ok) {
          const data = await response.json();
          setNickname(data.nickname || '');
          setActorList(data.preferredActors || []);
          setGenreList(data.preferredGenres || []);
        } else {
          alert('    .');
        }
      } catch (error) {
        console.error('  :', error);
        alert('     .');
      } finally {
        setLoading(false);
      }
    };

    fetchUserData();
  }, [memberId]);

  const handleNicknameChange = async () => {
    if (!nickname.trim()) {
      alert(' .');
      return;
    }
    try {
      const token = localStorage.getItem('token');
      await fetch(`/member/nickname/${memberId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ nickname }),
      });
      alert('  .');
    } catch (error) {
      console.error('  :', error);
      alert('  .');
    }
  };

  const handlePasswordChange = async () => {
    if (!password.trim()) {
      alert(' .');
      return;
    }
    try {
      const token = localStorage.getItem('token');
      await fetch(`/member/password/${memberId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ password }),
      });
      alert('  .');
      setPassword('');
    } catch (error) {
      console.error('  :', error);
      alert('  .');
    }
  };

  const handleAddActor = async () => {
    if (!newActor.trim()) {
      alert('  .');
      return;
    }
  
    try {
      const token = localStorage.getItem('token'); //   
      const response = await fetch(`/survey/${memberId}/actor`, {
        method: 'POST',
        headers: {
          'Content-Type': 'text/plain', // Content-Type text/plain 
          Authorization: `Bearer ${token}`, //   
        },
        body: newActor.trim(), //   
      });
  
      if (response.ok) {
        //    
        setActorList((prev) => [...prev, newActor.trim()]);
        setNewActor(''); //   
        alert('   .');
      } else {
        //     
        const errorMessage = await response.text();
        alert(`  : ${errorMessage}`);
      }
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    }
  };
  
  const handleRemoveActor = async (preferredActorId) => {
    try {
      const token = localStorage.getItem('token'); //   
      const response = await fetch(`/survey/preferred-actors/${preferredActorId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`, //   
        },
      });
  
      if (response.ok) {
        //     
        setActorList((prev) => prev.filter((item) => item.id !== preferredActorId));
        alert('   .');
      } else {
        //      
        const errorMessage = await response.text();
        alert(`  : ${errorMessage}`);
      }
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    }
  };
  
  const handleAddGenre = async () => {
    if (!newGenre.trim()) {
      alert('  .');
      return;
    }
  
    try {
      const token = localStorage.getItem('token'); //   
      const response = await fetch(`/survey/${memberId}/genre`, {
        method: 'POST',
        headers: {
          'Content-Type': 'text/plain', // Content-Type text/plain 
          Authorization: `Bearer ${token}`, //   
        },
        body: newGenre.trim(), //   
      });
  
      if (response.ok) {
        //    
        setGenreList((prev) => [...prev, newGenre.trim()]);
        setNewGenre(''); //   
        alert('   .');
      } else {
        //     
        const errorMessage = await response.text();
        alert(`  : ${errorMessage}`);
      }
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    }
  };  

  const handleRemoveGenre = async (preferredGenreId) => {
    try {
      const token = localStorage.getItem('token'); //   
      const response = await fetch(`/survey/preferred-genres/${preferredGenreId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`, //   
        },
      });
  
      if (response.ok) {
        //     
        setGenreList((prev) => prev.filter((item) => item.id !== preferredGenreId));
        alert('   .');
      } else {
        //      
        const errorMessage = await response.text();
        alert(`  : ${errorMessage}`);
      }
    } catch (error) {
      console.error('  :', error); //  
      alert('    .');
    }
  };
  

  if (loading) {
    return <div> ...</div>;
  }

  return (
    <div className="edit-page">
      <h2 className="edit-title">  </h2>

      <div className="form-section">
        <label className="form-label"> </label>
        <div className="input-group">
          <input
            type="text"
            className="form-input"
            value={nickname}
            onChange={(e) => setNickname(e.target.value)}
            placeholder=" "
          />
          <button
            type="button"
            className="form-button"
            onClick={handleNicknameChange}
          >
            
          </button>
        </div>
      </div>

      <div className="form-section">
        <label className="form-label"> </label>
        <div className="input-group">
          <input
            type="password"
            className="form-input"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder=" "
          />
          <button
            type="button"
            className="form-button"
            onClick={handlePasswordChange}
          >
            
          </button>
        </div>
      </div>

      <div className="form-section">
        <label className="form-label">  </label>
        <div className="input-group">
          <input
            type="text"
            className="form-input"
            value={newActor}
            onChange={(e) => setNewActor(e.target.value)}
            placeholder="  "
          />
          <button
            type="button"
            className="form-button"
            onClick={handleAddActor}
          >
            
          </button>
        </div>
        <div className="actor-list">
          {actorList.length > 0 ? (
            actorList.map((actor, index) => (
              <div key={index} className="actor-item">
                {actor}
                <button
                  type="button"
                  className="delete-button"
                  onClick={() => handleRemoveActor(actor.id)}
                >
                  X
                </button>
              </div>
            ))
          ) : (
            <p>  .</p>
          )}
        </div>
      </div>

      <div className="form-section">
        <label className="form-label">  </label>
        <div className="input-group">
          <input
            type="text"
            className="form-input"
            value={newGenre}
            onChange={(e) => setNewGenre(e.target.value)}
            placeholder="  "
          />
          <button
            type="button"
            className="form-button"
            onClick={handleAddGenre}
          >
            
          </button>
        </div>
        <div className="genre-list">
          {genreList.length > 0 ? (
            genreList.map((genre, index) => (
              <div key={index} className="genre-item">
                <span>{genre}</span>
                <button
                  type="button"
                  className="delete-button"
                  onClick={() => handleRemoveGenre(genre)}
                >
                  X
                </button>
              </div>
            ))
          ) : (
            <p>  .</p>
          )}
        </div>
      </div>


      <button type="button" className="back-button" onClick={() => navigate(-1)}>
        
      </button>
    </div>
  );
};

export default Edit;
