import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom'; // navigate   import
import Navbar from '../ui/Navbar';
import { tmdbApiClient } from '../../api/tmdb'; // Axios  
import MovieList from '../list/MovieList'; // MovieList  
import '../styles/ViewPage.css'; //   

const ViewPage = () => {
  const [movies, setMovies] = useState([]);
  const navigate = useNavigate(); // navigate  

  useEffect(() => {
    //     
    const fetchMovies = async () => {
      try {
        const response = await tmdbApiClient.get('/movie/popular', {
          params: {
            language: 'ko-KR', //    ()
          },
        });
        setMovies(response.data.results); //   
      } catch (error) {
        console.error('   :', error);
      }
    };

    fetchMovies();
  }, []);

  //        
  const handleMovieClick = (movieId) => {
    console.log('Navigating to movie details with ID:', movieId); //  
    navigate(`/api/movies/detail/${movieId}?language=ko`);
  };

  return (
    <div className="view-page">
      <Navbar />
      
      <div className="view-content">
        <h2>  </h2>
        <MovieList movies={movies} onMovieClick={handleMovieClick} /> {/* MovieList   */}
      </div>
    </div>
  );
};

export default ViewPage;
