import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import StarRating from './StarRating';
import Navbar from '../ui/Navbar';
import axios from 'axios';
import '../styles/MovieDetails.css';

const RegiMovieDel = () => {
  const { movieId } = useParams();
  const navigate = useNavigate();

  const [movieDetails, setMovieDetails] = useState(null); //   
  const [credits, setCredits] = useState(null); //   
  const [rating, setRating] = useState(0); // 
  const [reviewId, setReviewId] = useState(null); //  ID  
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchMovieAndReviews = async () => {
      if (!movieId) {
        console.error(' ID .');
        return;
      }

      try {
        const token = localStorage.getItem('token');
        const memberId = localStorage.getItem('memberId');
        if (!token || !memberId) {
          alert(' .');
          navigate('/'); //   
          return;
        }

        const language = 'ko';

        //      
        const [detailsResponse, creditsResponse, reviewsResponse] = await Promise.all([
          axios.get(`/api/movies/detail/${movieId}`, {
            params: { language },
            headers: { Authorization: `Bearer ${token}` },
          }),
          axios.get(`/api/movies/${movieId}/credits`, {
            params: { language },
            headers: { Authorization: `Bearer ${token}` },
          }),
          axios.get(`/member/user/${memberId}`, {
            headers: { Authorization: `Bearer ${token}` },
          }),
        ]);

        setMovieDetails(detailsResponse.data);
        setCredits(creditsResponse.data);

        // `reviews`   `movieId`   
        const review = reviewsResponse.data.reviews.find(
          (r) => r.movieId === Number(movieId)
        );

        if (review) {
          setRating(Number(review.ranked)); //   
          setReviewId(review.reviewId); //  ID 
        } else {
          setRating(0); //    0
        }
      } catch (error) {
        console.error('    :', error.response?.data || error.message);
        alert('     .');
      } finally {
        setLoading(false);
      }
    };

    fetchMovieAndReviews();
  }, [movieId, navigate]);

  const handleDelete = async () => {
    const confirmDelete = window.confirm(' ?');
    if (!confirmDelete) return;

    try {
      const token = localStorage.getItem('token');
      if (!token) {
        alert(' .');
        return;
      }

      await axios.delete(`/review/${reviewId}`, {
        headers: { Authorization: `Bearer ${token}` },
      });

      alert(' .');
      navigate(-1); //   
    } catch (error) {
      console.error('   :', error.response?.data || error.message);
      alert('  .  .');
    }
  };

  if (loading) {
    return (
      <div className="movie-details">
        <Navbar />
        <div className="loading-container">
          <p>Loading...</p>
        </div>
      </div>
    );
  }

  if (!movieDetails || !credits) {
    return (
      <div className="movie-details">
        <Navbar />
        <div className="error-container">
          <p>   .  .</p>
        </div>
      </div>
    );
  }

  const imageUrl = movieDetails.poster_path
    ? `https://image.tmdb.org/t/p/w500${movieDetails.poster_path}`
    : '/icons/default-image-url.jpg';

  const cast = credits.cast?.slice(0, 3) || []; //  3 
  const director = credits.crew?.find((person) => person.job === 'Director') || null;

  return (
    <div className="movie-details">
      <Navbar />
      <div className="movie-details-container">
        <div className="movie-image">
          <img src={imageUrl} alt={movieDetails.title || ' '} />
        </div>
        <div className="movie-info">
          <h2>{movieDetails.title || ' '}</h2>
          <p><strong>:</strong> {movieDetails.release_date || ' '}</p>
          <p><strong>:</strong> {movieDetails.genres?.map((g) => g.name).join(', ') || ' '}</p>
          <p><strong>:</strong> {movieDetails.overview || ' '}</p>
          <p><strong>:</strong> {director?.name || ' '}</p>
          <p><strong> :</strong> {cast.map((c) => c.name).join(', ') || ' '}</p>
          <div className="star-rating">
            <p><strong>:</strong></p>
            <StarRating
              rating={rating} //   
              onRatingChange={setRating}
              readOnly={!!reviewId} //     
            />
          </div>
          <div className="button-container">
            {reviewId ? (
              <button className="delete-button" onClick={handleDelete}> </button>
            ) : (
              <button className="register-button"> </button>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default RegiMovieDel;
