import React, { useState, useEffect } from 'react';
import '../styles/StarRating.css';

const StarRating = ({ onRatingChange, rating: initialRating = 0, readOnly = false }) => {
  const [rating, setRating] = useState(Number(initialRating)); //   
  const [hover, setHover] = useState(null);

  useEffect(() => {
    console.log('Initial Rating:', initialRating); //   rating 
    setRating(Number(initialRating)); //    
  }, [initialRating]);

  const handleRating = (value) => {
    if (readOnly) return; //    
    setRating(value); // rating 
    if (onRatingChange) {
      onRatingChange(value); //    
    }
  };

  return (
    <div className="star-rating">
      {[...Array(5)].map((_, index) => {
        const value = index + 1;
        const isFilled = value <= (hover || rating); // hover   rating  

        return (
          <span
            key={index}
            className={`star ${isFilled ? 'filled' : ''}`} //    
            onMouseEnter={() => !readOnly && setHover(value)} // hover  
            onMouseLeave={() => !readOnly && setHover(null)} // hover  
            onClick={() => handleRating(value)} //    
            style={{ cursor: readOnly ? 'default' : 'pointer' }} //    
          >
            
          </span>
        );
      })}
    </div>
  );
};

export default StarRating;
