import React, { useEffect, useState } from 'react';
import { useParams, useLocation, useNavigate } from 'react-router-dom';
import StarRating from './StarRating';
import Navbar from '../ui/Navbar';
import axios from 'axios';
import '../styles/MovieDetails.css';

const MovieDetails = () => {
  const { movieId } = useParams(); // URL  ID 
  const location = useLocation();
  const navigate = useNavigate();

  const [movieDetails, setMovieDetails] = useState(null); //    
  const [credits, setCredits] = useState(null); //     
  const [rating, setRating] = useState(0); //    

  useEffect(() => {
    //       
    const fetchMovieDetails = async () => {
      if (!movieId) {
        console.error(' ID .');
        return;
      }

      try {
        const language = new URLSearchParams(location.search).get('language') || 'ko';

        const [detailsResponse, creditsResponse] = await Promise.all([
          axios.get(`/api/movies/detail/${movieId}`, {
            params: { language },
            headers: { Authorization: `Bearer YOUR_ACCESS_TOKEN_HERE` },
          }),
          axios.get(`/api/movies/${movieId}/credits`, {
            params: { language },
            headers: { Authorization: `Bearer YOUR_ACCESS_TOKEN_HERE` },
          }),
        ]);

        setMovieDetails(detailsResponse.data); //    
        setCredits(creditsResponse.data); //     
      } catch (error) {
        console.error('      :', error.response?.data || error.message);
        alert('     .    .');
      }
    };

    fetchMovieDetails();
  }, [movieId, location.search]);

  const handleRegister = async () => {
    const confirmRegistration = window.confirm('?');
    if (!confirmRegistration) {
      return;
    }
  
    try {
      const token = localStorage.getItem('token');
      if (!token) {
        alert(' .');
        return;
      }
  
      const memberId = localStorage.getItem('memberId');
      if (!memberId) {
        alert('    .  .');
        return;
      }
  
      //     
      await axios.post(
        `/review/${memberId}`,
        {
          movieId: movieDetails.id || movieDetails.movieId, //  ID
          ranked: rating, //   
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json',
          },
        }
      );
  
      alert('  .');
      navigate(-1);
    } catch (error) {
      console.error('    :', error.response?.data || error.message);
      alert('  .  .');
    }
  };
  

  if (!movieDetails || !credits) {
    return (
      <div className="movie-details">
        <Navbar />
        <p>Loading...</p>
      </div>
    );
  }

  const imageUrl = movieDetails.poster_path
    ? `https://image.tmdb.org/t/p/w500${movieDetails.poster_path}`
    : '/icons/default-image-url.jpg';

  const handleImageError = (e) => {
    e.target.src = '/icons/default-image-url.jpg'; //      
  };

  //  3    
  const cast = credits.cast?.slice(0, 3) || []; //  3 
  const director = credits.crew?.find(person => person.job === 'Director') || null; //  

  return (
    <div className="movie-details">
      <Navbar />
      <div className="movie-details-container">
        <div className="movie-image">
          <img
            src={imageUrl}
            alt={movieDetails.title || ' '}
            onError={handleImageError}
          />
        </div>
        <div className="movie-info">
          <h2>{movieDetails.title || ' '}</h2>
          <p><strong>:</strong> {movieDetails.release_date || ' '}</p>
          <p><strong>:</strong> {movieDetails.genres?.map(genre => genre.name).join(', ') || ' '}</p>
          <p><strong>:</strong> {movieDetails.overview || ' '}</p>
          <p><strong>:</strong> {director?.name || ' '}</p>
          <p><strong> :</strong> {cast.map(actor => actor.name).join(', ') || ' '}</p>
          <div className="star-rating">
            <p><strong>:</strong></p>
            <StarRating rating={rating} onRatingChange={setRating} />
          </div>
          <div className="register-button-container">
            <button className="register-button" onClick={handleRegister}></button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MovieDetails;
