import React, { useEffect, useState } from 'react';
import Navbar from '../ui/Navbar';
import MovieList from '../list/MovieList';
import '../styles/SearchPage.css';
import { useLocation, useNavigate } from 'react-router-dom';

const SearchPage = () => {
  const [searchResults, setSearchResults] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const location = useLocation();
  const navigate = useNavigate();

  useEffect(() => {
    // URL   
    const queryParams = new URLSearchParams(location.search);
    const results = queryParams.get('results');
    const query = queryParams.get('query');

    try {
      const parsedResults = results ? JSON.parse(decodeURIComponent(results)) : [];
      setSearchResults(parsedResults);
    } catch (error) {
      console.error('   :', error);
    }

    if (query) {
      setSearchTerm(query);
    }
  }, [location.search]);

  const handleMovieClick = (movieId) => {
    console.log('Navigating to movie details with ID:', movieId);
    navigate(`/api/movies/detail/${movieId}?language=ko`); //    
  };

  return (
    <div className="search-page">
      <Navbar />
      <div className="search-content">
        {searchResults.length === 0 && (
          <p className="no-results-message">  .</p>
        )}
        {searchResults.length > 0 && (
          <div className="movie-category">
            <h2>'{searchTerm}'  </h2>
            <MovieList movies={searchResults} onMovieClick={handleMovieClick} />
          </div>
        )}
      </div>
    </div>
  );
};

export default SearchPage;