import React, { useState } from 'react';
import '../styles/Chatbot.css';

const Chatbot = ({ isOpen, onClose }) => {
  const [messages, setMessages] = useState([]); //  
  const [input, setInput] = useState(''); //  
  const userName = ''; //  
  const aiName = 'AI'; // AI 
  const userProfile = 'https://via.placeholder.com/40?text=U'; //   
  const aiProfile = 'https://via.placeholder.com/40?text=AI'; // AI  

  //   
  const handleSend = async () => {
    if (!input.trim()) {
      alert(' .');
      return;
    }

    const timestamp = new Date().toLocaleTimeString(); //  
    const userMessage = {
      sender: 'user',
      text: input,
      time: timestamp,
      name: userName,
      profile: userProfile,
    };

    setMessages((prev) => [...prev, userMessage]);

    try {
      const token = localStorage.getItem('token'); //  
      if (!token) {
        throw new Error(' .  .');
      }

      const response = await fetch('/api/ai/chatbot', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`, //  
        },
        body: JSON.stringify({ text: input }), //     
      });

      if (!response.ok) {
        throw new Error(`HTTP  : ${response.status}`);
      }

      const data = await response.json();

      const aiMessage = {
        sender: 'ai',
        text: data[0]?.llm_response || data.llm_response || 'AI  ',
        time: new Date().toLocaleTimeString(),
        name: aiName,
        profile: aiProfile,
      };

      setMessages((prev) => [...prev, aiMessage]);
    } catch (error) {
      console.error('AI     :', error);

      const errorMessage = {
        sender: 'ai',
        text: 'AI   .    .',
        time: new Date().toLocaleTimeString(),
        name: aiName,
        profile: aiProfile,
      };

      setMessages((prev) => [...prev, errorMessage]);
    }

    setInput(''); //  
  };

  if (!isOpen) return null; //    

  return (
    <div className="chat-window">
      <div className="chat-header">
        <h4></h4>
        <button onClick={onClose} className="chat-close-button"></button>
      </div>
      <div className="chat-messages">
        {messages.map((msg, index) => (
          <div
            key={index}
            className={`chat-message ${
              msg.sender === 'user' ? 'user-message' : 'bot-message'
            }`}
          >
            <img
              src={msg.profile}
              alt={`${msg.name} profile`}
              className="chat-profile"
            />
            <div>
              <div className="chat-message-header">
                {msg.name}  {msg.time}
              </div>
              <div className="chat-message-content">{msg.text}</div>
            </div>
          </div>
        ))}
      </div>
      <div className="chat-input">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder=" ..."
        />
        <button onClick={handleSend}></button>
      </div>
    </div>
  );
};

export default Chatbot;
