import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import AdminNavbar from '../ui/AdminNavbar';
import axios from 'axios';
import '../styles/admin/AdminUsers.css';

function AdminUsers() {
  const [users, setUsers] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const memberId = localStorage.getItem('memberId');
    const token = localStorage.getItem('token');

    if (!memberId || memberId !== '1' || !token) {
      alert('  .');
      navigate('/');
    }

    const fetchUsers = async () => {
      try {
        const response = await axios.get('/member/users', {
          headers: { Authorization: `Bearer ${token}` },
        });
        setUsers(response.data);
      } catch (error) {
        console.error('     :', error);
        alert('    .');
      }
    };

    fetchUsers();
  }, [navigate]);

  //   
  const suspendMember = async (memberId) => {
    const token = localStorage.getItem('token');
    if (window.confirm(` ID ${memberId} ?`)) {
      try {
        const response = await axios.put(
          `/members/${memberId}/suspend`,
          {},
          {
            headers: { Authorization: `Bearer ${token}` },
          }
        );
        alert(response.data || '  .');

        //     
        setUsers((prevUsers) =>
          prevUsers.map((user) =>
            user.member.memberId === memberId
              ? { ...user, member: { ...user.member, memberStatus: 'SUSPENDED' } }
              : user
          )
        );
      } catch (error) {
        console.error('    :', error);
        alert('  .');
      }
    }
  };

  return (
    <div className="admin-page">
      <AdminNavbar />
      <div className="admin-content">
        <h1> </h1>
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>Member Name</th>
              <th>Nickname</th>
              <th>Role</th>
              <th>Member Status</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {users.map((user, index) => (
              <tr key={index}>
                <td>{user.member.memberId}</td>
                <td>{user.member.membername}</td>
                <td>{user.member.nickname}</td>
                <td>{user.member.role}</td>
                <td>{user.member.memberStatus}</td>
                <td>
                  <button
                    className="action-button"
                    onClick={() => suspendMember(user.member.memberId)}
                  >
                    
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default AdminUsers;
