import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import '../styles/Navbar.css';
import { FiLogOut, FiSearch } from 'react-icons/fi';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFilm } from '@fortawesome/free-solid-svg-icons';

function Navbar() {
  const [userId, setUserId] = useState(null); // userId  
  const [searchQuery, setSearchQuery] = useState('');
  const [accessToken, setAccessToken] = useState(null); // JWT   
  const navigate = useNavigate();

  //   userId JWT  
  useEffect(() => {
    const storedUserId = localStorage.getItem('memberId');
    const storedAccessToken = localStorage.getItem('token'); // JWT  

    setUserId(storedUserId);
    setAccessToken(storedAccessToken); //  
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('memberId');
    localStorage.removeItem('token'); // JWT  
    alert(' .');
    navigate('/'); //   
  };

  const handleSearch = async () => {
    if (!searchQuery.trim()) {
      alert('  .');
      return;
    }

    try {
      const response = await axios.get('/api/movies/search', {
        params: {
          query: searchQuery,
          includeAdult: false,
          language: 'ko',
          page: 1,
        },
        headers: {
          Authorization: `Bearer ${accessToken}`, // JWT  
        },
      });

      if (response.data && response.data.results.length > 0) {
        const searchResults = response.data.results;
        navigate(
          `/movie/search?results=${encodeURIComponent(
            JSON.stringify(searchResults)
          )}&query=${encodeURIComponent(searchQuery)}`
        );
      } else {
        alert('  .');
      }
    } catch (error) {
      console.error(' :', error);
      alert('   .     .');
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSearch();
    }
  };

  const navigateTo = (path) => {
    if (!accessToken) {
      alert(' .');
      return;
    }

    if (userId) {
      navigate(path.replace(':userId', userId));
    } else {
      alert(' .');
    }
  };

  return (
    <nav className="navbar">
      <div className="navbar-container">
        <div className="logo">
          <FontAwesomeIcon icon={faFilm} size="2x" alt="Movie Icon" />
        </div>
        <ul className="nav-links">
          <li>
            <button
              className="nav-link-button"
              onClick={() => navigateTo('/main/:userId')}
            >
              Home
            </button>
          </li>
          <li>
            <button
              className="nav-link-button"
              onClick={() => navigateTo('/movie/view')}
            >
               
            </button>
          </li>
          <li>
            <button
              className="nav-link-button"
              onClick={() => {
                if (!accessToken) {
                  alert(' .');
                  return;
                }
                navigateTo('/api/ai/predict/:userId');
              }}
            >
               
            </button>
          </li>
          <li>
            <button
              className="nav-link-button"
              onClick={() => navigateTo('/my/:userId')}
            >
               
            </button>
          </li>
        </ul>
        <div className="search-bar">
          <input
            type="text"
            placeholder=" ..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            onKeyPress={handleKeyPress}
          />
          <button onClick={handleSearch} className="search-button">
            <FiSearch size={20} />
          </button>
          <button className="logout-button" onClick={handleLogout}>
            <FiLogOut size={20} />
          </button>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
