import React, { useEffect, useState } from 'react';
import { tmdbApiClient } from '../../api/tmdb';
import '../styles/UpcomingMovies.css';

const UpcomingMovies = () => {
  const [upcomingMovies, setUpcomingMovies] = useState([]);
  const [error, setError] = useState(null);


  useEffect(() => {
    const fetchUpcomingMovies = async () => {
      try {
        const response = await tmdbApiClient.get('/movie/upcoming', {
          params: {
            language: 'ko-KR',
            region: 'KR',
          },
        });
        setUpcomingMovies(response.data.results.slice(0, 2)); //  2  
      } catch (err) {
        console.error('       :', err);
        setError('    .');
      }
    };

    fetchUpcomingMovies();
  }, []);

  if (error) return <p>{error}</p>;
  if (!upcomingMovies.length) return <p> ...</p>;

  return (
    <div className="upcoming-movies">
      <h2>  </h2>
      <div className="movie-list">
        {upcomingMovies.map((movie) => (
          <div className="movie-card" key={movie.id}>
            <img
              src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`}
              alt={movie.title}
              className="movie-poster"
            />
            <p className="movie-title">{movie.title}</p>
            <p className="movie-release-date">: {movie.release_date}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default UpcomingMovies;
