import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import AdminNavbar from '../ui/AdminNavbar';
import axios from 'axios';
import '../styles/admin/AdminUsers.css'; //   

function AdminSurvey() {
  const [surveys, setSurveys] = useState([]); //    
  const navigate = useNavigate();

  useEffect(() => {
    const memberId = localStorage.getItem('memberId');
    const token = localStorage.getItem('token');

    //   
    if (!memberId || memberId !== '1' || !token) {
      alert('  .');
      navigate('/');
    }

    //   
    const fetchSurveys = async () => {
      try {
        const response = await axios.get('/member/users', {
          headers: {
            Authorization: `Bearer ${token}`, //    
          },
        });
        setSurveys(response.data); //   
      } catch (error) {
        console.error('     :', error);
        alert('    .');
      }
    };

    fetchSurveys();
  }, [navigate]);

  return (
    <div className="admin-page">
      <AdminNavbar />
      <div className="admin-content">
        <h1>  </h1>
        <table>
          <thead>
            <tr>
              <th>ID</th>
              <th>Member Name</th>
              <th>Age</th>
              <th>Gender</th>
              <th>Preferred Genres</th>
            </tr>
          </thead>
          <tbody>
            {surveys.map((survey, index) => (
              <tr key={index}>
                <td>{survey.member.memberId}</td>
                <td>{survey.member.membername}</td>
                <td>{survey.survey.age}</td>
                <td>{survey.survey.gender}</td>
                <td>{survey.preferredGenres.join(', ')}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default AdminSurvey;
