import React from 'react';
import '../styles/MovieItem.css';

const MovieItem = ({ movie, onClick }) => {
  const handleImageError = (e) => {
    e.target.src = '/icons/default-image-url.jpg'; //       
  };

  const imageUrl = movie.poster_path
    ? `https://image.tmdb.org/t/p/w500${movie.poster_path}`
    : movie.imageUrl || '/icons/default-image-url.jpg'; //       

  return (
    <div className="movie-item" onClick={() => onClick && onClick(movie.id || movie.movieCd)}>
      <img
        src={imageUrl}
        alt={movie.title || movie.movieNm}
        onError={handleImageError} //       
      />
      <p className="movie-title">{movie.title || movie.movieNm}</p> {/*    */}
    </div>
  );
};

export default MovieItem;
