import React, { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import '../styles/SurveyPage.css';

const genresList = [
  '', '', '', '', '', '',
  '', '', '', '', '()', '',
  '', '()', 'SF', '', '', '/', '',
];

const SurveyPage = () => {
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const userId = searchParams.get('userId');
  
  const navigate = useNavigate();
  const [actors, setActors] = useState('');
  const [actorList, setActorList] = useState([]);
  const [selectedGenres, setSelectedGenres] = useState([]);
  const [gender, setGender] = useState('');
  const [age, setAge] = useState('');

  const addActor = () => {
    if (actors.trim() !== '') {
      setActorList([...actorList, actors]);
      setActors('');
    }
  };

  const removeActor = (index) => {
    const newActorList = actorList.filter((_, i) => i !== index);
    setActorList(newActorList);
  };

  const handleGenreChange = (genre) => {
    if (selectedGenres.includes(genre)) {
      setSelectedGenres(selectedGenres.filter((item) => item !== genre));
    } else {
      setSelectedGenres([...selectedGenres, genre]);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const surveyData = {
      gender: gender,
      age: age,
      preferredGenres: selectedGenres,
      preferredActors: actorList,
    };

    try {
      const response = await fetch(`/survey/${userId}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(surveyData),
      });

      if (response.ok) {
        alert('  !');
        navigate('/'); //      
      } else {
        alert('  .   .');
      }
    } catch (error) {
      console.error('Error:', error);
      alert(' .     .');
    }
  };

  return (
    <div className="survey-page">
      <h2>!   .</h2>
      <p>   .</p>
      <form className="survey-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="age"></label>
          <input
            type="number"
            id="age"
            name="age"
            min="10"
            max="100"
            value={age}
            onChange={(e) => setAge(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="gender"></label>
          <select
            id="gender"
            name="gender"
            value={gender}
            onChange={(e) => setGender(e.target.value)}
          >
            <option value=""></option>
            <option value="M"></option>
            <option value="F"></option>
          </select>
        </div>
        <div className="form-group">
          <label> </label>
          <div className="actor-input-group">
            <input
              type="text"
              value={actors}
              onChange={(e) => setActors(e.target.value)}
              placeholder="  "
            />
            <button type="button" onClick={addActor}></button>
          </div>
          <div className="actor-list">
            {actorList.map((actor, index) => (
              <div key={index} className="actor-item">
                {actor}
                <button type="button" onClick={() => removeActor(index)}>X</button>
              </div>
            ))}
          </div>
        </div>
        <div className="form-group">
          <label>  </label>
          <div className="checkbox-group">
            {genresList.map((genre, index) => (
              <div key={index} className="checkbox-item">
                <input
                  type="checkbox"
                  id={`genre-${index}`}
                  value={genre}
                  checked={selectedGenres.includes(genre)}
                  onChange={() => handleGenreChange(genre)}
                />
                <label htmlFor={`genre-${index}`}>{genre}</label>
              </div>
            ))}
          </div>
        </div>
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default SurveyPage;
