import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../styles/RegisterPage.css';

const RegisterPage = () => {
    const [nickname, setNickname] = useState('');
    const [membername, setMembername] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const navigate = useNavigate();

    const handleRegister = async (e) => {
        e.preventDefault();

        //  :    
        if (!nickname || !membername || !password || !confirmPassword) {
            alert('   .');
            return;
        }

        //  
        if (password !== confirmPassword) {
            alert('  .');
            return;
        }

        //  
        const data = {
            membername: membername,
            password: password,
            nickname: nickname,
        };

        try {
            //  
            const response = await fetch('/member/signup', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(data),
            });

            if (response.ok) {
                const result = await response.json(); //  
                console.log(' :', result); //  
                const memberId = result.id; //   memberId 

                if (!memberId) {
                    console.error("Member ID is missing in the response.");
                    alert("     .   .");
                    return;
                }

                //    
                alert('  !');
                navigate(`/survey?userId=${memberId}`);            } else {
                //   
                const errorResult = await response.json();
                console.error(' :', errorResult.message || 'Unknown error');
                alert(` . : ${errorResult.message || '  .'}`);
            }
        } catch (error) {
            //     
            console.error('   :', error);
            alert(' .     .');
        }
    };

    return (
        <div className="register-page">
            <form onSubmit={handleRegister} className="register-form">
                <h2></h2>
                <input
                    type="text"
                    placeholder=""
                    value={nickname}
                    onChange={(e) => setNickname(e.target.value)}
                />
                <input
                    type="email"
                    placeholder=" ()"
                    value={membername}
                    onChange={(e) => setMembername(e.target.value)}
                />
                <input
                    type="password"
                    placeholder=""
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                />
                <input
                    type="password"
                    placeholder=" "
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                />
                <button type="submit"></button>
            </form>
        </div>
    );
};

export default RegisterPage;
